import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store'
// import Login from '../views/Login'
// import PostLogin from '../views/PostLogin'
import NotFount from '../views/NotFount'
import Logout from '../views/Logout'
import Home from '../views/Home'
import Multas from '../components/Multas/ListMultas'
import Ocupacion from '../components/Ocupacion/ListOcupacion'
import HrsLabor from '../components/HrsLabor/ListHrsLabor'
import Volumetrias from '../components/Volumetrias/ListVolumetrias'
import Ans from '../components/Ans/ListAns'

Vue.use(VueRouter)

const routes = [
  // {
  //   path: '/app/login',
  //   name: 'Login',
  //   component: Login
  // },
  // {
  //   path: '/app/postLogin',
  //   name: 'PostLogin',
  //   component: PostLogin
  // },
  {
    path: '/app/logout',
    name: 'Logout',
    component: Logout,
    // meta: {
    //   requiresAuth: true,
    // }
  },
  {
    path: '/app/',
    name: 'Home',
    component: Home,
    meta: {
      requiresAuth: true,
    }
  },
  {
    path: '/app/multas',
    name: 'Multas',
    component: Multas,
    meta: {
      requiresAuth: true,
    }
  },
  {
    path: '/app/ocupacion',
    name: 'Ocupacion',
    component: Ocupacion,
    meta: {
      requiresAuth: true,
    }
  },
  {
    path: '/app/horas_labor',
    name: 'HrsLabor',
    component: HrsLabor,
    meta: {
      requiresAuth: true,
    }
  },
  {
    path: '/app/volumetrias',
    name: 'Volumetrias',
    component: Volumetrias,
    meta: {
      requiresAuth: true,
    }
  },
  {
    path: '/app/ans',
    name: 'Ans',
    component: Ans,
    meta: {
      requiresAuth: true,
    }
  },
  {
    path: '/app/404',
    name: '404',
    component: NotFount
  },
  {
    path: '/app/*',
    redirect: '/app/404'
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  linkActiveClass: 'is.active',
  routes
})

router.beforeEach((to, from, next) => {
  if (to.name == 'Multas' || to.name == 'Ocupacion' || to.name == 'HrsLabor' || to.name == 'Ans' || to.name == 'Volumetrias'){
    store.dispatch("loadDataAll")
  }
  next()
})

export default router
