<template>
	<v-container>
		<v-dialog v-model="dialog" persistent max-width="700px">
			<v-card>
				<v-card-title>
					<span class="headline">{{ formTitle }}</span>
				</v-card-title>
				<v-card-text>
					<v-form ref="form" v-model="valid" lazy-validation>
						<v-container>
							<v-row>
								<v-col cols="12" md="4">
									<v-select
										:items="dataPeriodos"
										item-text="periodo"
										item-value="id"
										label="Periodo"
										v-model="editItem.periodo"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
								<v-col cols="12" md="4">
									<v-select
										:items="paises"
										item-text="text"
										item-value="id"
										label="Pais"
										v-model="editItem.pais"
										:rules="[v => !!v || 'Campo requerido']"
										:disabled="disabledPais"
										required
									></v-select>
								</v-col>
								<v-col cols="12" md="4">
									<v-select
										:items="dataProyectos"
										item-text="proyecto"
										item-value="id"
										label="Proyecto"
										v-model="editItem.proyecto"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
							</v-row>
							<v-row>
								<v-col cols="12" md="4">
									<v-select
										:items="tipo_formato"
										label="Tipo de formato"
										v-model="editItem.tipo_formato"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
                <v-col cols="12" md="8">
									<v-text-field label="Servicio" v-model="editItem.servicio" :rules="[v => !!v || 'Campo requerido']" 
										required
									></v-text-field>
								</v-col>								
							</v-row>
							<v-row>
								<v-col cols="12" md="12">
									<v-text-field label="Ind. de Servicio" v-model="editItem.indicadores_servicio" 
										:rules="[v => !!v || 'Campo requerido']" required
									></v-text-field>
								</v-col>
							</v-row>
              <v-row>
								<v-col cols="12" md="4" v-if="showNSOP">
									<v-text-field v-model="editItem.nso" prefix="%" label="NSO" type="number" 
										:rules="[v => !!v || 'Campo requerido', v => v > 0 || 'Debe ser mayor a 0', v => v <= 100 || 'Debe ser menor a 100']"
										min="1" max="100">
									</v-text-field>
								</v-col>
                <v-col cols="12" md="4" v-if="showNSOD">
									<v-text-field v-model="editItem.nso_dec" type="number" label="NSO DEC" 
										:rules="[v => !!v || 'Campo requerido']">
									</v-text-field>
								</v-col>
								<v-col cols="12" md="4">
									<v-select :items="ind_txt_items" v-model="editItem.ind_txt" label="IND TEX" :rules="[v => !!v || 'Campo requerido']" required></v-select>
								</v-col>
								<v-col cols="12" md="4">
									<v-text-field v-model="editItem.obtenido" type="number" label="Obtenido" 
										:rules="[v => !!v || 'Campo requerido', v => v > 0 || 'Debe ser mayor a 0', v => v <= 100 || 'Debe ser menor a 100']"
										required>
									</v-text-field>
								</v-col>
							</v-row>
							<v-row>
                <v-col cols="12" md="9">
									<v-text-field v-model="editItem.observaciones" label="Observaciones"></v-text-field>
								</v-col>
								<v-col cols="12" md="3">
									<v-text-field v-model="editItem.cumplimiento_general" label="Cump. Gral"
										type="number" disabled
									></v-text-field>
								</v-col>
							</v-row>
						</v-container>
					</v-form>
				</v-card-text>
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn color="blue darken-1" text @click="close()">Cancelar</v-btn>
					<v-btn color="blue darken-1" text @click="save()">Guardar</v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>
	</v-container>
</template>
<script>
  import http from '@/plugins/axios'
  import swal from 'sweetalert'
  import { mapState } from 'vuex'
  export default {
    name: 'EditAns',
    data: () => ({
			valid: true,
			showNSOP: false,
			showNSOD: false,
			ind_txt_items: ['<','<=','>=','>']
    }),
    methods: {  
      close() {
				this.showNSOP = false
				this.showNSOD = false
        this.$store.commit('updateShowEdit')
				this.$store.commit('updateEditItem', {})
				this.$store.commit('setFormTitle', '')
				this.$refs.form.resetValidation()
      },
      put(url, params){
        http.put(url, params)
        .then((response) => {
          let data = response.data
          this.$store.commit("updateDataAns", data)
          swal("Actualizado Correctamente!", {
            icon: "success",
          });
        })
        .catch(() => {
					swal("Error Interno", "Favor contactar con el administrador", "error");
        });
			},
			post(url, params){
				http.post(url, params)
					.then((response) => {
						let data = response.data
						this.$store.commit("newAns", data)
						swal("Creado Correctamente!", {
							icon: "success",
						});
					})
					.catch(() => {
						swal("Error Interno", "Favor contactar con el administrador", "error");
					});
			},
			calcular_cumplimiento(){
				let ind_txt = this.editItem.ind_txt
				let tipo_formato = this.editItem.tipo_formato;
				let nsop = parseFloat(this.editItem.nso);
				let nsod = parseFloat(this.editItem.nso_dec);
				let obt = parseFloat(this.editItem.obtenido);
				switch(ind_txt){
					case "<":
						if (tipo_formato === 'PORCENTAJE'){
							if (nsop < obt){
								this.editItem.cumplimiento_general = 1
							}else{
								this.editItem.cumplimiento_general = -1
							}
						}else{
							if (nsod < obt){
								this.editItem.cumplimiento_general = 1
							}else{
								this.editItem.cumplimiento_general = -1
							}
						}
						break;
					case "<=":
						if (tipo_formato === 'PORCENTAJE'){
							if (nsop <= obt){
								this.editItem.cumplimiento_general = 1
							}else{
								this.editItem.cumplimiento_general = -1
							}
						}else{
							if (nsod <= obt){
								this.editItem.cumplimiento_general = 1
							}else{
								this.editItem.cumplimiento_general = -1
							}
						}
						break;
					case ">":
						if (tipo_formato === 'PORCENTAJE'){
							if (nsop > obt ){
								this.editItem.cumplimiento_general = 1
							}else{
								this.editItem.cumplimiento_general = -1
							}
						}else{
							if (nsod > obt ){
								this.editItem.cumplimiento_general = 1
							}else{
								this.editItem.cumplimiento_general = -1
							}
						}					
						break;
					case ">=":
						if (tipo_formato === 'PORCENTAJE'){
							if (nsop >= obt){
								this.editItem.cumplimiento_general = "1"
							}else{
								this.editItem.cumplimiento_general = "-1"
							}
						}else{
							if (nsod >= obt){
								this.editItem.cumplimiento_general = "1"
							}else{
								this.editItem.cumplimiento_general = "-1"
							}
						}
						break;
				}
			},
      save(){
				if (this.$refs.form.validate()){
					let proyecto = this.editItem.proyecto
					let periodo = this.editItem.periodo
					if (typeof(proyecto) !== "object"){
						this.editItem.proyecto = {"id": proyecto}
					}else{
						this.editItem.proyecto = {"id": this.editItem.proyecto.id}
					}
					if (typeof(periodo) !== "object"){
						this.editItem.periodo = {"id": periodo}
					}else{
						this.editItem.periodo = {"id": this.editItem.periodo.id}
					}
					this.calcular_cumplimiento()
					if (this.formTitle === 'Editar'){
						this.put(`api/ans/${this.editItem.id}`, this.editItem)
					}else{
						this.post('api/ans', this.editItem)
					}
					this.close()
				}
      },
		},
		watch: {
			"editItem.tipo_formato": function(value){
				if (value === "DECIMAL"){
					this.editItem.nso = null
					this.showNSOD = true
					this.showNSOP = false
				}else	if (value === "PORCENTAJE"){
					this.editItem.nso_dec = null
					this.showNSOP = true
					this.showNSOD = false
				}else{
					console.log("nada")
				}
			}
		},
    computed: {
      ...mapState(['dialog', 'dataProyectos', 'editItem', 'dataPeriodos', 'dataPjs', 'editItemIndex', 'paises', 'semanas', 'formTitle', 'tipo_formato']),
			disabledPais() {
				return this.$store.getters.getDisabledPais
			},
		},
  };
</script>

<style >
	.my-input input{ text-transform: uppercase}
</style>