<template>
	<v-container>
		<v-dialog v-model="dialog" persistent max-width="700px">
			<v-card>
				<v-card-title>
					<span class="headline">{{ formTitle }}</span>
				</v-card-title>
				<v-card-text>
					<v-form ref="form" v-model="valid" lazy-validation>
						<v-container>
							<v-row>
								<v-col cols="12" md="4">
									<v-select
										:items="dataPeriodos"
										item-text="periodo"
										item-value="id"
										label="Periodo"
										v-model="editItem.periodo"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
								<v-col cols="12" md="4">
									<v-select
										:items="paises"
										item-text="text"
										item-value="id"
										label="Pais"
										v-model="editItem.pais"
										:rules="[v => !!v || 'Campo requerido']"
										:disabled="disabledPais"
										required
									></v-select>
								</v-col>
								<v-col cols="12" md="4">
									<v-select
										:items="dataProyectos"
										item-text="proyecto"
										item-value="id"
										label="Proyecto"
										v-model="editItem.proyecto"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
							</v-row>
							<v-row>
								<v-col cols="12" md="8">
									<v-text-field label="Servicio" v-model="editItem.servicio" 
										:rules="[v => !!v || 'Campo requerido']" required>
									</v-text-field>
								</v-col>
								<v-col cols="12" md="4">
									<v-text-field v-model="editItem.linea_base_horas" type="number" label="Linea Base Horas" 
										@change="change_Lhrs" :rules="[v => !!v || 'Campo requerido', v => parseFloat(v) > 0 || 'No puede ser igual 0']"
										required min="1" suffix="Hrs">
									</v-text-field>
								</v-col>
							</v-row>
							<v-row>
								<v-col cols="12" md="6">
									<v-text-field v-model="editItem.horas_ejecutadas" type="number" label="Horas Ejecutadas"
										@change="change_HrsE" :rules="[v => !!v || 'Campo requerido']"
										required min="1" suffix="Hrs">
									</v-text-field>
								</v-col>
								<v-col cols="12" md="6">
									<v-text-field v-model="editItem.uso" prefix="%" type="number" label="Uso" disabled></v-text-field>
								</v-col>
							</v-row>
						</v-container>
					</v-form>
				</v-card-text>
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn color="blue darken-1" text @click="close()">Cancelar</v-btn>
					<v-btn color="blue darken-1" text @click="save()">Guardar</v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>
	</v-container>
</template>
<script>
  import http from '@/plugins/axios'
  import swal from 'sweetalert'
	import { mapState } from 'vuex'
	
  export default {
    name: 'EditHrsLabor',
    data: () => ({
      valid: true,
    }),
    methods: {
			change_Lhrs(value){
        if (value !== "" || value != 0 ){
          this.editItem.uso = parseFloat(this.editItem.horas_ejecutadas / value * 100).toFixed(2) 
        }else{
          this.editItem.uso = 0
        }
      },
      change_HrsE(value){
        if (value !== "" || value != 0 ){
          this.editItem.uso = parseFloat(value / this.editItem.linea_base_horas * 100).toFixed(2) 
        }else{
          this.editItem.uso = 0
        }
      }, 
      close() {
        this.$store.commit('updateShowEdit')
				this.$store.commit('updateEditItem', {})
				this.$store.commit('setFormTitle', '')
				this.$refs.form.resetValidation()
      },
      put(url, params){
        http.put(url, params)
        .then((response) => {
          let data = response.data
          this.$store.commit("updateDataHrsLabor", data)
          swal("Actualizado Correctamente!", {
            icon: "success",
          });
        })
        .catch(() => {
					swal("Error Interno", "Favor contactar con el administrador", "error");
        });
			},
			post(url, params){
        http.post(url, params)
        .then((response) => {
          let data = response.data
          this.$store.commit("newHrsLabor", data)
          swal("Creado Correctamente!", {
            icon: "success",
          });
        })
        .catch(() => {
          swal("Error Interno", "Favor contactar con el administrador", "error");
        });
      },   
      save(){
				if (this.$refs.form.validate()){
					let proyecto = this.editItem.proyecto
					let periodo = this.editItem.periodo 
					if (typeof(proyecto) !== "object"){
						this.editItem.proyecto = {"id": proyecto}
					}else{
						this.editItem.proyecto = {"id": this.editItem.proyecto.id}
					}
					if (typeof(periodo) !== "object"){
						this.editItem.periodo = {"id": periodo}
					}else{
						this.editItem.periodo = {"id": this.editItem.periodo.id}
					}
					this.editItem.servicio = this.editItem.servicio.toUpperCase()
					if (this.formTitle === 'Editar'){
						this.put(`api/hrs_labor/${this.editItem.id}`, this.editItem)
					}else{
						this.post('api/hrs_labor', this.editItem)
					}
					this.close()
				}
      },
    },
    computed: {
			...mapState(['dialog', 'dataProyectos', 'editItem', 'dataPeriodos', 'editItemIndex', 'paises', 'formTitle']),
			disabledPais() {
				return this.$store.getters.getDisabledPais
			},
    },
  };
</script>

<style >
	.my-input input{ text-transform: uppercase}
</style>